<template>
  <div class="mechanism-info-container">

    <img class="back_arc" src="@/assets/images/my_top_back.png" alt="">

    <div class="logo-area f-col-between-c">
      <div class="logo-icon f-c">
        <div class="logo" :style="{ 'background-image': 'url( https://qds-files.oss-cn-qingdao.aliyuncs.com/image/1555296678_Wqv1DGLJ.jpg )' }" />
      </div>
      <p class="logo-name">
        美智思享幼儿园
      </p>
    </div>

    <div class="info">
      <div class="info-row f-row-between">
        <p class="label">
          地址
        </p>
        <p class="content">
          福建省福州市仓山区飞客小镇2号楼4层
        </p>
      </div>
      <div class="info-row f-row-between">
        <p class="label">
          园所性质
        </p>
        <p class="content">
          公立幼儿园
        </p>
      </div>
      <div class="info-row f-row-between">
        <p class="label">
          招生电话
        </p>
        <p class="content">
          12345678901
        </p>
      </div>
      <div class="info-row f-row-between">
        <p class="label">
          招生联系人
        </p>
        <p class="content">
          林老师
        </p>
      </div>
      <div class="info-row f-row-between">
        <p class="label">
          园长电话
        </p>
        <p class="content">
          12345678902
        </p>
      </div>
    </div>

  </div>
</template>

<script>
export default {
  created() {
    document.title = '园所信息';
  },
}
</script>

<style lang="scss" scoped>
.mechanism-info-container {
  width: 100%;
  min-height: 100%;
  background-color: #fff;

  .back_arc {
    position: absolute;
    width: 100%;
  }

  .logo-area {
    position: relative;
    padding-top: 20px;
    width: 100%;
    height: 135px;

    .logo-icon {
      width: 77px;
      height: 77px;
      border: 1px solid rgba(255,255,255,1);
      border-radius: 50%;

      .logo {
        background-position: center center;
        background-repeat: no-repeat;
        background-size: cover;
        width: 70px;
        height: 70px;
        border-radius: 50%;
      }
    }
    .logo-name {
      font-size: 18px;
      font-weight: 600;
      color: rgba(255,255,255,1);
    }
  }

  .info {
    width: calc(100% - 32px);
    margin: 11px 16px 0;
    padding: 12px 16px;
    background:rgba(255,255,255,1);
    box-shadow:0px 2px 7px 1px rgba(0, 0, 0, 0.12);
    border-radius:10px;
    position: relative;

    .info-row {
      width: 100%;
      box-sizing: border-box;
      padding: 12px 0;

      .label {
        width: 30%;
        font-size: 15px;
        color: rgba(153,153,153,1);
      }
      .content {
        width: 65%;
        font-size: 15px;
        color: rgba(51,51,51,1);
        line-height: 1.5;
      }
    }
  }
}
</style>


